*
{
	margin: 0;
	padding: 0;
}

h1
{
	font-size: 3.5vw;
	font-family: 'Lilita One', cursive;
}

h2
{
	font-size: 1.5vw;
	font-family: 'Source Sans Pro', sans-serif;
}

p,
input,
button
{
	font-size: 1.2vw;
	font-family: 'Lilita One', cursive;
}

.wrapper
{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100vh;

  	background: rgba(45,176,224,1);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45,176,224,1)), color-stop(100%, rgba(96,40,168,1)));
  background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.wrapper__main
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-flex-direction: row;
	        flex-direction: row;
	width: 60%;
	height: 60vh;
	background-color: #2a6bb1;
	box-shadow: 30px 36px 17px -10px rgba(0,0,0,0.75);
}

.info
{
	text-align: center;
	width: 40%;
	color: #fff;
	background-image: url(/static/media/1538665284.cc54c19b.jpg);
	background-size: cover;
	background-blend-mode: multiply;
  	background-color: #6A6A6A;
}

.info__title
{
	position: relative;
	top: 30%;
}

.info__text
{
	position: relative;
	top: 32%;
}

.main__UI
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 60%;
}

.form
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 100%;
}

.form__city
{
	font-weight: lighter;
	width: 39%;
	margin: 15% 0 0 5%;
	padding: 0 0 .5% 1%;
	color: #fff;
	background-color: transparent;
	border: none;
	border-bottom: 2px solid #000;
	outline: none;
}

.form__city::-webkit-input-placeholder {
  color: #FFFFFF;
}

.form__city::-moz-placeholder {
  color: #FFFFFF;
}

.form__btn
{
	width: 35%;
	height: 5.8vh;
	margin: 14% 0 0 5%;
	color: #fff;
	background-color: #235d9c;
	border: 1px solid #000;
	cursor: pointer;
	transition: .3s;
}

.form__btn:hover
{
	background-color: #1E64AE;
}

.form__btn:active
{
	background-color: transparent;
}

.weather
{
	width: 100%;
	margin: 5% 0 0 5%;
	color: #fff;
}

.weather__el
{
	margin-top: 2%;
}


 @media screen and (min-width: 764px) and (max-width: 1024px)
 {
	h1
	{
		font-size: 5vw;
	}

	h2
	{
		font-size: 2vw;
	}

	p,
	input,
	button
	{
		font-size: 2.2vw;
	}

	.wrapper__main
	{
		height: 85vh;
	}

	.info
	{
		width: 100%;
		height: 45vh;
	}

	.info__title
	{
		top: 40%;
	}

	.info__text
	{
		top: 45%;
	}



	.main__UI
	{
		width: 100%;
	}

	.form
	{
		margin-top: -10%;
	}

	.form__city
	{
		width: 30%;
		margin-top: 1%;
	}

	.form__btn
	{
		width: 40%;
		margin-top: 1%;
	}
 }

 @media screen and (min-width: 375px) and (max-width: 764px)
 {
	h1
	{
		font-size: 5vw;
	}

	h2
	{
		font-size: 2.8vw;
	}

	p,
	input,
	button
	{
		font-size: 2vw;
	}

	.wrapper__main
	{
		height: 85vh;
	}

	.info
	{
		width: 100%;
		height: 45vh;
	}

	.info__title
	{
		top: 40%;
	}

	.info__text
	{
		top: 45%;
	}



	.main__UI
	{
		width: 100%;
	}

	.form
	{
		margin-top: -10%;
	}

	.form__city
	{
		width: 30%;
		margin-top: 1%;
	}

	.form__btn
	{
		width: 40%;
		margin-top: 1%;
	}
 }
